import Vue from 'vue'
import Router from 'vue-router'
import PagesView from '../views/PagesView'
import HomeView from '../views/HomeView'
import ClassificationView from '../views/ClassificationView'
import ClassificationListView from '../views/ClassificationListView'
import SearchView from '../views/SearchView'
import CartView from '../views/CartView'
import MyShoppingView from '../views/MyShoppingView'
import GoodsDetailView from '../views/GoodsDetailView'
import GoodsListDetailView from '../views/GoodsListDetailView'
import LoginView from '../views/LoginView'
import RegisterView from '../views/RegisterView'
import SubmitCodeView from '../views/SubmitCodeView'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/pages/'
    },
    {
      path: '/pages',
      component: PagesView,
      children: [
        {
          path: '',
          redirect: '/pages/home'
        },
        {
          path: 'home',
          name: 'HomeView',
          component: HomeView
        },
        {
          path: 'classification',
          name: 'ClassificationView',
          component: ClassificationView,
          children:[
          	{
		          path: '',
		          redirect: '/pages/classification/list/001'
		        },
          	{
          		path:'list/:id',
          		name: 'ClassificationListView',
          		component: ClassificationListView
          	}
          ]
        },
        {
          path: 'search',
          name: 'SearchView',
          component: SearchView
        },
        {
          path: 'cart',
          name: 'CartView',
          component: CartView
        },
        {
          path: 'my',
          name: 'MyShoppingView',
          component: MyShoppingView
        },
        {
		      path: 'login',
		      name: 'LoginView',
		      component: LoginView
		    },
		    {
		      path: 'register',
		      name: 'RegisterView',
		      component: RegisterView
		    },
		    {
		      path: 'submitCode',
		      name: 'SubmitCodeView',
		      component: SubmitCodeView
		    },
        {
          path: 'goodsDetail/:id',
          name: 'GoodsDetailView',
          component: GoodsDetailView
        },
		    {
		      path: '/pages/classification/:classify/goodsList/:id',
		      name: 'GoodsListDetailView',
		     	component: GoodsListDetailView
		    }
      ]
    },
    {
      path: '*',
      redirect: '/pages/'
    }
  ]
})



