<template>
	<div class="page">
		<header-bar type="classificationTop"></header-bar>
		<goods-types :items="goodsTitleList"></goods-types>
		<router-view></router-view>
	</div>

</template>

<script>
	import { mapState } from 'vuex'
	import HeaderBar from '../components/HeaderBar'
	import GoodsTypes from '../components/GoodsTypes'
	export default {
		name: 'ClassificationView',
		components: { HeaderBar,GoodsTypes },
		data() {
			return {
				
			}
		},
		computed: {
		    ...mapState({
		      goodsTitleList: state => state.classification.goodsTitleList
		    })
		},
		methods:{
			getGoodsNameList: function () {
		     	this.$store.dispatch('getGoodsTitleList');
		   }
		},
		created(){
			this.getGoodsNameList();
		}
	}
</script>

<style lang="scss" scoped>
	.page{
		background: white;
	}
</style>


