import Vue from 'vue'

const state = {
  list: [],
  his_list: []
}

const mutations = {
  getList (state, payload) {
    state.list = payload.res.list
    state.his_list = payload.res.his_list
  },
  deleteHis_list(state, payload){
  	state.his_list = [];
  }
}

const actions = {
  getList ({ commit }, payload) {
    Vue.http.jsonp('http://www.my9hao.com/mobile/index.php?act=index&op=search_key_list')
            .then(res => {
              commit({
                type: 'getList',
                res: res.body.datas
              })
            })
    
  }
}

export default {
  state,
  mutations,
  actions
}



