import Vue from 'vue'

const state = {
  imgUrl:'',
  products: []
}

const mutations = {
  getImgUrl (state, payload) {
    state.imgUrl = payload.res[0].adv_list.item[0].image
    state.products = payload.res[1].goods
  }
}

const actions = {
  getImgUrl ({commit}) {
    Vue.http.jsonp('http://www.my9hao.com/mobile/index.php?act=index').then(res => {
              commit({
                type: 'getImgUrl',
                res: res.body.datas
              })
            })
  }
}

export default {
  state,
  mutations,
  actions
}



