<template>
	<div class="header">
		<template v-if="type === 'homeTop'">
			<div class="logo"><img src="../assets/img/home_logo.png"/></div>
			<div class="search">
				<router-link  :to="{ name: 'SearchView'}">
		  			<span></span>请输入关键词
		     	</router-link>
			</div>
			<div class="msg">
				<router-link  :to="{ name: 'CartView'}">
		  			<span></span>
		     	</router-link>
			</div>
		</template>
		<template v-if="type === 'classificationTop'">
			<div class="goReturn"><a href="javascipt:;" @click="routerBack"><img src="../assets/img/arrow_left_b.png"/></a></div>
			<div class="search width">
				<router-link  :to="{ name: 'SearchView'}">
		  			<span></span>请输入关键词
		     	</router-link>
			</div>
			<div class="more">
				<a href="javascript:;">
		  			<span></span>
		     	</a>
			</div>
		</template>
		<template v-if="type === 'searchTop'">
			<div class="goReturn"><a href="javascipt:;" @click="routerBack"><img src="../assets/img/arrow_left_b.png"/></a></div>
			<div class="search width">
				<router-link  :to="{ name: 'SearchView'}">
		  			<span></span><input type="text" name="search" id="search" v-model="value" placeholder="请输入搜索关键词"/>
		     	</router-link>
			</div>
			<div class="searchBtn">
				<router-link :to="'/pages/classification/search/goodsList/' + this.value" >
		  			<span>搜索</span>
		     	</router-link>
			</div>
		</template>
		<template v-if="type === 'CartTop'">
			<div class="goReturn"><a href="javascipt:;" @click="routerBack"><img src="../assets/img/arrow_left_b.png"/></a></div>
			<div class="cart">
				购物车
			</div>
			<div class="more">
				<a href="javascript:;">
		  			<span></span>
		     	</a>
			</div>
		</template>
	</div>
	
</template>

<script>
	export default {
		name: 'HeaderBar',
		props:["type"],
		data() {
			return {
				value:''
			}
		},
		methods:{
			routerBack: function () {
		      	this.$router.go(-1);
		  }
		}
	}
</script>

<style lang='scss' scoped>
.header{
	position: fixed;
	top: 0;
	width: 100%;
	height: 0.9rem;
	font-size: 0.28rem;
	color: white;
	background: #F8F8F8;
	border-bottom: 1px solid #eeeeee;
	z-index: 999;
	.logo{
		width: 1.32rem;
		height: 0.6rem;
		margin:0.12rem 0.3rem;
		float:left;
		img{
			width: 100%;
			height: 100%;
		}
	}
	.search{
		float: left;
		width: 4.76rem;
		height: 0.66rem;
		line-height: 0.6rem;
		background: white;
		border: 1px solid #dedede;
		padding-left: 0.05rem;
		margin: 0.12rem 0;
		border-radius: 0.1rem;
		a{
			width: 100%;
			height: 100%;
			color: #bdbdbd;
			display: inline-block;
			span{
				display: inline-block;
				width: 0.44rem;
				height: 0.44rem;
				opacity: 0.5;
				vertical-align: middle;
				background: url(../assets/img/search_ico.png) no-repeat center;
				background-size: 70%;
			}
			input{
				border: none;
				height: 100%;
				outline: none;
			}
		}
		
	}
	.search.width{
		width: 5.5rem;
	}
	.cart{
		float: left;
		width: 100%;
		height: 0.9rem;
		text-align: center;
		line-height: 0.9rem;
		color: #333;
		font-size: 0.34rem;
		position: absolute;
	}
	.msg{
		width: 0.52rem;
		height: 0.52rem;
		float: left;
		margin: 0.15rem;
		a{
			span{
				display: block;
				width: 100%;
				height: 100%;
				background: url(../assets/img/message_b.png) no-repeat;
				background-size: 90%;
				opacity: 0.5;
			}
		}
	}
	.goReturn{
		position: relative;
		float: left;
		width: 0.32rem;
		height: 0.32rem;
		margin: 0.29rem;
		z-index: 999;
		img{
			width: 100%;
			height: 100%;
		}
	}
	.more{
		width: 0.52rem;
		height: 0.52rem;
		float: right;
		margin: 0.2rem;
		a{
			span{
				display: block;
				width: 100%;
				height: 100%;
				background: url(../assets/img/more_b.png) no-repeat;
				background-size: 90%;
				opacity: 0.5;
			}
			
		}
	}
	.searchBtn{
		width: 0.9rem;
		height: 100%;
		line-height: 0.9rem;
		float: right;
		font-size: 0.3rem;
		a{
			color: #333;
		}
	}
	
}

</style>



