<template>
	<div class="list">
		<template v-if="items.length != 0">
			<scroller ref="my_scroller" 
				:on-refresh="refresh" 
				:on-infinite="infinite" 
				:snapping="snapping" 
				:snap-height="snapHeight" 
				:animationDuration="animationDuration" 
				refresh-layer-color="#4b8bf4"
      			loading-layer-color="#ec4949">
			    <svg class="spinner" style="stroke: #4b8bf4;" slot="refresh-spinner" viewBox="0 0 64 64">
			        <g stroke-width="7" stroke-linecap="round">
			        	<line x1="10" x2="10" y1="27.3836" y2="36.4931">
				        	<animate attributeName="y1" dur="750ms" values="16;18;28;18;16;16" repeatCount="indefinite"></animate>
				        	<animate attributeName="y2" dur="750ms" values="48;46;36;44;48;48" repeatCount="indefinite"></animate>
				        	<animate attributeName="stroke-opacity" dur="750ms" values="1;.4;.5;.8;1;1" repeatCount="indefinite"></animate>
				        </line>
			        	<line x1="24" x2="24" y1="18.6164" y2="45.3836"><animate attributeName="y1" dur="750ms" values="16;16;18;28;18;16" repeatCount="indefinite"></animate>
			        		<animate attributeName="y2" dur="750ms" values="48;48;46;36;44;48" repeatCount="indefinite"></animate>
			        		<animate attributeName="stroke-opacity" dur="750ms" values="1;1;.4;.5;.8;1" repeatCount="indefinite"></animate>
			        	</line>
			        	<line x1="38" x2="38" y1="16.1233" y2="47.8767">
			        		<animate attributeName="y1" dur="750ms" values="18;16;16;18;28;18" repeatCount="indefinite"></animate>
			        		<animate attributeName="y2" dur="750ms" values="44;48;48;46;36;44" repeatCount="indefinite"></animate>
			        		<animate attributeName="stroke-opacity" dur="750ms" values=".8;1;1;.4;.5;.8" repeatCount="indefinite"></animate>
			        	</line>
			        	<line x1="52" x2="52" y1="16" y2="48">
			        		<animate attributeName="y1" dur="750ms" values="28;18;16;16;18;28" repeatCount="indefinite"></animate>
			        		<animate attributeName="y2" dur="750ms" values="36;44;48;48;46;36" repeatCount="indefinite"></animate>
			        		<animate attributeName="stroke-opacity" dur="750ms" values=".5;.8;1;1;.4;.5" repeatCount="indefinite"></animate>
			        	</line>
			        </g>
			    </svg>
				<div :class="type">
					<ul class="goods-secrch-list">
						<li class="goods-item" v-for="item in items">
							<router-link :to="'/pages/goodsDetail/' + item.goods_id ">
								<span class="goods-pic">
									<img :src="item.goods_image_url">
								</span>
								<dl class="goods-info">
									<dt class="goods-name">
										<h4>{{item.goods_name}}</h4>
										<h6>{{item.goods_jingle}}</h6>
									</dt>
									<dd class="goods-sale">
										<span class="goods-price">￥<em>{{item.goods_price}}</em>
										</span>
									</dd>
									<dd class="goods-assist">
										<span class="goods-sold">销量
											<em>{{item.goods_salenum}}</em>
										</span>
										<div class="goods-store">
											<a href="javascript:void(0);" ><span>{{item.store_name}}</span><i></i></a>
										</div>
									</dd>
								</dl>
							</router-link>	
						</li>
					</ul>
				</div>
				<svg class="spinner" style="fill: #ec4949;" slot="infinite-spinner" viewBox="0 0 64 64">
			        <g>
			        	<circle cx="16" cy="32" stroke-width="0" r="3">
			        		<animate attributeName="fill-opacity" dur="750ms" values=".5;.6;.8;1;.8;.6;.5;.5" repeatCount="indefinite"></animate>
			        		<animate attributeName="r" dur="750ms" values="3;3;4;5;6;5;4;3" repeatCount="indefinite"></animate>
			        	</circle>
			        	<circle cx="32" cy="32" stroke-width="0" r="3.09351">
			        		<animate attributeName="fill-opacity" dur="750ms" values=".5;.5;.6;.8;1;.8;.6;.5" repeatCount="indefinite"></animate>
			        		<animate attributeName="r" dur="750ms" values="4;3;3;4;5;6;5;4" repeatCount="indefinite"></animate>
			        	</circle>
			        	<circle cx="48" cy="32" stroke-width="0" r="4.09351">
			        		<animate attributeName="fill-opacity" dur="750ms" values=".6;.5;.5;.6;.8;1;.8;.6" repeatCount="indefinite"></animate>
			        		<animate attributeName="r" dur="750ms" values="5;4;3;3;4;5;6;5" repeatCount="indefinite"></animate>
			        	</circle>
			        </g>
				</svg>
   
			</scroller>	
		</template>
		<template v-else>
			<div class="nctouch-norecord search">
				<div class="norecord-ico"><i></i></div>
					<dl>
						<dt>没有找到任何相关信息</dt>
						<dd>选择或搜索其它商品分类/名称...</dd>
					</dl>
				<a href="javascript:history.go(-1)" class="btn">重新选择</a>
			</div>
		</template>
	</div>
</template>

<script>
	export default {
		name: 'GoodsList',
		props: ["items","type"],
		data() {
			return {
				snapping: true,
		        snapHeight: 50,
		        animationDuration: 150
			}
		},
		methods: {
		   infinite:function(done){
		        setTimeout(() => {
		        	if (!this.$store.state.goodlist.hasMore) {
		            	done(true)
		            	return
		            }else{
		            	this.$emit('moreList')
		            	done()
		            }
		        }, 1500)
		   },
		   refresh:function(done){
		   	 	setTimeout(() => {
		        	if (true) {
		            	done(true)
		            	return
		            }else{
//		            	this.$emit('moreList')
		            	done()
		            }
		        }, 1500)
		   }
	    }
	}
</script>

<style lang='scss' scoped>
	.list{
		width: 100%;
		float: left;
	}
	.goods-grid{
		width: 100%;
		background: #f5f5f5;
		float: left;
		ul{
			width: 100%;
			float: left;
			li{
				width: 50%;
				height: 100%;
				float: left;
				padding: 0.1rem;
				a{
					float: left;
					width: 100%;
					height: 100%;
					background: white;
					color: #999999;
					.goods-pic{
						float: left;
						width: 100%;
						height: 100%;
						img{
							width: 100%;
							height: 100%;
						}
					}
					dl{
						float: left;
						width: 100%;
						height: 1.5rem;
						padding: 0 0.11rem;
						dt{
							h4{
								width: 100%;
								font-size: 0.28rem;
							    line-height: 0.34rem;
							    overflow: hidden;
							    color: #111;
							    font-weight: 400;
							    overflow: hidden;
								text-overflow:ellipsis;
								white-space: nowrap;
							}
							h6{
								display: none;
							}
						}
						dd:nth-of-type(1){
							color: #ec5464;
							font-size: 0.34rem;
							padding: 0.08rem 0;
							em{
								font-style: normal;
							}
						}
						dd:nth-of-type(2){
							padding-bottom: 0.08rem;
							font-size: 0.24rem;
							.goods-sold{
								float: left;
								em{
									color: #111;
									font-style: normal;
								}
							}
							.goods-store{
								float: right;
								width: 60%;
								white-space: nowrap;
    							overflow: hidden;
    							span{
    								display: none;
    							}
								i{
									background-image: url(../assets/img/more_b.png);
								    background-repeat: no-repeat;
								    background-position: 50% 50%;
								    background-size: 60%;
								    vertical-align: top;
								    display: inline-block;
								    width: 0.44rem;
								    height: 0.44rem;
								    opacity: 0.4;
								    float: right;
								}
							}
						}
					}
				}
			}
		}
	}
	.goods-List{
		width: 100%;
		background: white;
		float: left;
		ul{
			width: 100%;
			li{
				width: 100%;
				height: 2.32rem;
				a{
					color: #999999;
					.goods-pic{
						float: left;
						width: 2.32rem;
						height: 100%;
						padding: 0.16rem;
						img{
							width: 100%;
							height: 100%;
						}
					}
					dl{
						float: left;
						width: 5.18rem;
						height: 100%;
						border-bottom:1px solid #eee ;
						padding:0.16rem 0.16rem 0 0 ;
						dt{
							h4{
								font-size: 0.28rem;
							    line-height: 0.34rem;
							    height: 0.68rem;
							    overflow: hidden;
							    color: #111;
							    font-weight: 400;
							}
							h6{
								font-size: 0.24rem;
							    color: #999;
							    line-height: 0.4rem;
							    height: 0.4rem;
							    white-space: nowrap;
							    text-overflow: ellipsis;
							    overflow: hidden;
							    font-weight: 400;
							}
						}
						dd:nth-of-type(1){
							color: #ec5464;
							font-size: 0.34rem;
							padding: 0.08rem 0;
							em{
								font-style: normal;
							}
						}
						dd:nth-of-type(2){
							padding-bottom: 0.08rem;
							font-size: 0.24rem;
							.goods-sold{
								float: left;
								em{
									color: #111;
									font-style: normal;
								}
							}
							.goods-store{
								float: right;
								width: 60%;
								white-space: nowrap;
    							overflow: hidden;
    							span{
    								display: inline-block;
    								width: 100%;
    								overflow: hidden; 
									text-overflow: ellipsis;
									white-space: nowrap;
    							}
								i{
									background-image: url(../assets/img/more_b.png);
								    background-repeat: no-repeat;
								    background-position: 50% 50%;
								    background-size: 60%;
								    vertical-align: top;
								    display: inline-block;
								    width: 0.44rem;
								    height: 0.44rem;
								    opacity: 0.4;
								    display: none;
								}
							}
						}
					}
				}
			}
		}
	}
	.nctouch-norecord{
		position: absolute;
	    left: 50%;
	    top: 50%;
	    width: 4.4rem;
	    height: 3.6rem;
	    margin-left: -2.2rem;
	    margin-top: -3.6rem;
	    text-align: center;
	    .norecord-ico{
	    	display: inline-block;
		    width: 1.32rem;
		    height: 1.32rem;
		    padding: 0.16rem;
		    margin: 0 auto;
		    background-color: #DDD;
		    border-radius: 100%;
		    i{
		    	background-image: url(../assets/img/search_w.png);
		    	display: block;
			    width: 100%;
			    height: 100%;
			    background-repeat: no-repeat;
			    background-position: 50% 50%;
			    background-size: 80%;
		    }
	    }
	    dl{
	    	height: 1.2rem;
			margin-top: 0.34rem;
			dt{
				display: block;
			    margin-bottom: 0.11rem;
			    font-size: 0.32rem;
			    line-height: 0.44rem;
			}
			dd{
				display: block;
			    margin-bottom: 0.22rem;
			    font-size: 0.24rem;
			    line-height: 0.44rem;
			    color: #999;
			}
	    }
	    .btn{
	    	vertical-align: top;
		    display: inline-block !important;
		    height: 0.48rem;
		    padding: 0 0.12rem;
		    margin: 0 auto;
		    font-size: 0.26rem;
		    color: #555 !important;
		    line-height: 0.48rem;
		    text-align: center;
		    background-color: #FFF;
		    border: solid 1px #CCC;
		    border-radius: 0.1rem;
	    }
	}
</style>



