import Vue from 'vue'
const state = {
  goodsList: [],
  skip:5,
  curpage:1,
  urlTmps:'',
  list:'getGoodsList',
  moreList:'getMoreGoodsList',
  hasMore:true
}

const mutations = {
  getGoodsList (state, payload) {
  	state.curpage = 1
    state.goodsList = payload.res.datas.goods_list
  },
  getMoreGoodsList (state, payload) {
  	if(state.hasMore){
  			state.goodsList = state.goodsList.concat(payload.res.datas.goods_list)
  	}
    
  }
}

const actions = {
  getGoodsList ({commit, state}, payload) {
      switch (payload.classify) {
        case 'list':
        	state.urlTmp = 'http://www.my9hao.com/mobile//index.php?act=goods&op=goods_list&page=5&curpage=1&gc_id='+payload.id
          break
				case 'recommends':
					state.urlTmp = 'http://www.my9hao.com/mobile//index.php?act=goods&op=goods_list&page=5&curpage=1&b_id='+payload.id
          break          
        case 'search':
        	state.urlTmp = 'http://www.my9hao.com/mobile//index.php?act=goods&op=goods_list&page=5&curpage=1&keyword='+payload.id
          break
        default:
          console.log('[Error]:Api is error!')
      }
      Vue.http.jsonp(state.urlTmp).then(res => {
                    commit({
                      type:  state.list,
                      res: res.body
                    })
                    state.hasMore = res.body.hasmore
                  })    
  },
  getMoreGoodsList ({commit, state}, payload) {
  	state.curpage++  
      switch (payload.classify) {
        case 'list':
        	state.urlTmp = 'http://www.my9hao.com/mobile//index.php?act=goods&op=goods_list&page='+state.skip+'&curpage='+state.curpage+'&gc_id='+payload.id
          break
				case 'recommends':
					state.urlTmp = 'http://www.my9hao.com/mobile//index.php?act=goods&op=goods_list&page='+state.skip+'&curpage='+state.curpage+'&b_id='+payload.id
          break          
        case 'search':
        	state.urlTmp = 'http://www.my9hao.com/mobile//index.php?act=goods&op=goods_list&page='+state.skip+'&curpage='+state.curpage+'&keyword='+payload.id
          break
        default:
          console.log('[Error]:Api is error!')
      }
      Vue.http.jsonp(state.urlTmp).then(res => {
                    commit({
                      type: state.moreList,
                      res: res.body
                    })
                    state.hasMore = res.body.hasmore
                  })               
  }
}

export default {
  state,
  mutations,
  actions
}



