// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import store from './store'
import VueScroller from 'vue-scroller'
import VueForm from 'vue-form'
Vue.use(VueResource)
Vue.use(VueScroller)
Vue.use(VueForm,{
	inputClasses: {
  	valid: 'form-control-success',
    invalid: 'form-control-danger'
  },
  validators: {
  	matches: function (value, attrValue) {
      if(!attrValue) {
      	return true;
      }
      return value === attrValue;
    },
    'name-strength': function (value) {
    	return /^[0-9A-Za-z]{6,20}$/.test(value);
    },
    'password-strength': function (value) {
    	return /^[0-9A-Za-z]{6,20}$/.test(value);
    },
    'email-strength': function (value) {
    	return /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/.test(value);
    },
    'phone-strength':function(value){
    	return /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/.test(value);
    },
    'imgcode-strength': function (value) {
    	return /^[0-9A-Za-z]{4}$/.test(value);
    }
  }
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



