<template>
  <div id="register_view">
  	<register-header title="会员注册"></register-header>
  	<div class="nav">
  		<ul>
  			<li :class="{'active':index == 1 }" @click="switchTab(1)">
  				<a href="javascript:;"><i class="reg"></i>普通注册</a>
  			</li>
  			<li :class="{'active':index == 2 }" @click="switchTab(2)">
  				<a href="javascript:;"><i class="regm"></i>手机注册</a>
  			</li>
  		</ul>
  	</div>
  	<component :is='currentView' keep-alive></component>
  	
  </div>
</template>

<script>
import RegisterHeader from '../components/RegisterHeader'
import NormalRegistration from '../components/NormalRegistration'
import PhoneRegistration from '../components/PhoneRegistration'
export default {
  name: 'RegisterView',
  components: { RegisterHeader,NormalRegistration,PhoneRegistration },
  data () {
    return {
     	isC:true,
     	index:1,
     	currentView:'NormalRegistration'
    }
  },
  methods: {
  	switchTab:function(index){
  		this.index = index
  		if(index == 1){
  			this.currentView = NormalRegistration
  		}else{
  			this.currentView = PhoneRegistration
  		}
  	}
  }
}
</script>
<style lang="scss" scoped>
#register_view{
	width: 100%;
	height: 100%;
	
	.nav{
		width: 100%;
		height: 0.88rem;
		border-bottom: 1px solid #eeeeee;
		background: white;
		ul {
			width:100%;
			height: 100%;
			li{
				width: 50%;
				height: 100%;
				float: left;
				line-height: 0.88rem;
				text-align: center;
				a{
					color: #333333;
					display: inline-block;
					height: 100%;
					width: 1.6rem;
					font-size: 0.24rem;
					i{
						display: inline-block;
						width: 0.3rem;
						height: 0.3rem;
						vertical-align: middle;
						margin: 0 0.1rem;
						filter: grayscale(100%);
						-webkit-filter:grayscale(100%);
					}
					.reg{
						background: url(../assets/img/reg_r.png);
						background-size: 100% 100%;
					}
					.regm{
						background: url(../assets/img/regm_r.png);
						background-size: 100% 100%;
					}
				}
			}
			.active{
				a{
					color:#ed5564;
					border-bottom: 2px solid #ed5564;
					i{
						filter: grayscale(0);
						-webkit-filter:grayscale(0);
					}
				}
			}
		}
	}
}
</style>



