<template>
	<div class="page">
		<header-bar v-if='headerIsTrue' type="homeTop" ></header-bar>
		<div class="banner">
			<img :src="imgUrl"/>
			<div class="gradient"></div>
			<div class="search">
				<router-link  :to="{ name: 'SearchView'}">
		  			<span></span>请输入关键词
		     	</router-link>
				<router-link  :to="{ name: 'CartView'}">
			  			<span>消息</span>
			    </router-link>
			</div>
			
		</div>
		<nav-btn></nav-btn>
		<home-list :items="products.item" :title="products.title"></home-list>
		<nctouch-footer ></nctouch-footer>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import HeaderBar from '../components/HeaderBar'
	import NavBtn from '../components/NavBtn'
	import HomeList from '../components/HomeList'
	import NctouchFooter from '../components/NctouchFooter'
	
	export default {
		name: 'HomeView',
		components: { HeaderBar,NavBtn,HomeList,NctouchFooter },
		data() {
			return {
				headerIsTrue:true
			}
		},
		computed: {
		    ...mapState({
		      imgUrl: state => state.home.imgUrl,
		      products: state => state.home.products,
		    })
		},
		methods:{
			getMovie: function () {
		      this.$store.dispatch('getImgUrl')
		   }
		},
		created:function(){
			this.getMovie();
		}
	}
</script>

<style lang="scss" scoped>
	.page{
		width: 100%;
		float: left;
		padding-bottom: 1rem;
	}
	.banner{
		width: 100%;
		height: 2.8rem;
		position: relative;
		z-index: 9999;
		img{
			width: 100%;
			height: 100%;
		}
		.gradient{
			width: 100%;
			height: 0.66rem;
			background-image: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.0));
			position: absolute;
			top: 0;
		}
		.search{
			width: 100%;
			height: 0.88rem;
			position: absolute;
			bottom: 0;
			a:nth-child(1){
				float: left;
				width: 6.2rem;
				height: 0.6rem;
				line-height: 0.6rem;
				color: #bdbdbd;
				background-color: rgba(255, 255, 255, 0.9);
				border: 1px solid #dedede;
				border-radius: 0.1rem;
				margin-top:0.14rem;
				margin-left:0.24rem;
				span{
					display: inline-block;
					width: 0.44rem;
					height: 0.44rem;
					opacity: 0.5;
					vertical-align: middle;
					background: url(../assets/img/search_ico.png) no-repeat center;
					background-size: 70%;
				}
			}
			a:nth-child(2){
				float: left;
				width: 0.96rem;
				height: 0.88rem;
				text-align: center;
				background: url(../assets/img/message_ws.png) no-repeat center top;
				background-size: 0.44rem 0.44rem;
				padding-top: 0.46rem;
				span{
					width: 100%;
					height: 100%;
					color: white;
				}
			}
		}
	}
</style>


