<template>
	<div class="classification">
		<scroller>
			<template v-if= "type === 'recommends'">
				<ul class="recommends">
					<li v-for = "item in items">
						<router-link :to="'/pages/classification/recommends/goodsList/' + item.brand_id">
				          	<img :src="item.brand_pic"/>
				          	<p>{{item.brand_name}}</p>
				        </router-link>
					</li>
				</ul>
			</template>
			<template v-else>
				<dl v-for="(item,index) in items" class="listMain">
					<dt>
						<router-link :to="'goodsList/' + item.gc_id">
				          	<span v-bind:style="{background: colors[Math.floor(Math.random()*10)]}" ></span>{{item.gc_name}}<span></span>
				        </router-link>
					</dt>
					<dd v-for = "i in item.child">
						<router-link :to="'goodsList/' + i.gc_id">
				          	{{i.gc_name}}
				        </router-link>
					</dd>
				</dl>
			</template>
		</scroller>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	export default {
		name: 'ClassificationMain',
		props:["items","type"],
		data() {
			return {
				colors:["#ED5564","#AC92ED","#4FC0E8","#A0D468","#48CFAE","#EC87BF","#FFCE55","#5D9CEC","#FB6E52","#AAB2BD"]
			}
		}
	}
</script>

<style lang='scss' scoped>
	.classification{
		width: 100%;
		.recommends{
			width: 100%;
			li{
				width: 33.3%;
				float: left;
				height: 1.94rem;
				padding: 0.32rem;
				img{
					width: 100%;
					margin: 0.22rem 0;
				}
				p{
					text-align: center;
					color: #333333;
					
				}
			}
		}
		.listMain{
			width: 100%;
			float: left;
			dt{
				width: 100%;
				height: 0.64rem;
				background: #fcfcfc;
				a{
					width: 100%;
					height: 100%;
					line-height: 0.64rem;
					float: left;
					color: #111;
					border: 1px solid #f5f5f5;
					padding-left: 0.22rem;
					span:nth-child(1){
						display: inline-block;
						width: 0.18rem;
						height: 0.18rem;
						border-radius: 50%;
						margin-right: 0.14rem;
					}
					span:nth-child(2){
						display: inline-block;
						width: 0.28rem;
						height: 0.28rem;
						float: right;
						background: url(../assets/img/arrow_right_b.png) no-repeat;
						background-size: 100% 100%;
						opacity: 0.5;
						margin-top: 0.18rem;
						margin-right: 0.22rem;
					}
				}
			}
			dd{
				width: 33%;
				height: 0.64rem;
				line-height: 0.64rem;
				float: left;
				padding-left: 0.22rem;
				margin: 0.1rem 0;
				a{
					color: #444444;
				}
			}
		}
	}
</style>



