<template>
	<div class="page">
		<header-bar type="classificationTop"></header-bar>
		<sorting  @changeList="changeList"></sorting>
		<goods-list :items = "goodsList" :type="addActive" @moreList="infiniteOne"></goods-list>
	</div>

</template>

<script>
	import { mapState, mapActions } from 'vuex'
	import HeaderBar from '../components/HeaderBar'
	import Sorting from '../components/Sorting'
	import GoodsList from '../components/GoodsList'
	export default {
		name: 'ClassDetailView',
		components: { HeaderBar,Sorting,GoodsList },
		data() {
			return {
				addActive:'goods-List',
				id:'',
				classify:''
			}
		},
		computed: {
		    ...mapState({
		      goodsList: state => state.goodlist.goodsList
		    })
		},
		methods:{
			getGoodsList: function () {
		     	this.id = this.$route.params.id
			    this.classify = this.$route.params.classify
			    this.$store.dispatch({
			      type: 'getGoodsList',
			      id: this.id,
			      classify: this.classify
			    }).then(res => {
			    })
		   },
		   changeList:function(){
		   		this.addActive = this.addActive === 'goods-grid' ? 'goods-List' : 'goods-grid'
		   },
		   infiniteOne:function(){
	   			this.triggerGetMoreGoodsList()
		   },
		   ...mapActions(['getMoreGoodsList']),
		   triggerGetMoreGoodsList (){
		      this.getMoreGoodsList({
		      		type: 'getMoreGoodsList',
			     	 id: this.id,
			      	classify: this.classify
		      })
		   }
		},
		created:function(){
			this.getGoodsList();
		}
	}
</script>

<style lang="scss" scoped>
	.page{
		position: absolute;
		top: 1.8rem;
		bottom: 1rem;
		width: 100%;
	}
</style>


