<template>
	<div class="type">
		<scroller :on-refresh="refresh" ref="my_scroller" class="my-scroller">
			<ul>
				<li>
					<router-link  :to="{name: 'ClassificationListView', params: { id: '001' }}" >
			  				<span><img src="../assets/img/degault.png"/></span>
			  				<span>品牌推荐</span>
			        </router-link>
				</li>
				<li v-for=" item in items">
					<router-link  :to="{name: 'ClassificationListView', params: { id: item.gc_id }}" >
			  				<span><img src="../assets/img/degault.png"/></span>
			  				<span>{{item.gc_name}}</span>
			        </router-link>
				</li>
			</ul>
			<span>&nbsp;</span>
		</scroller>
	</div>
</template>

<script>
	export default {
		name: 'GoodsTypes',
		props: ["items"],
		data() {
			return {}
		},
		methods: {
	      refresh:function(done) {
	        setTimeout(() => {
	          done()
	        }, 1500)
	      }
	    }
	}
</script>

<style lang='scss' >
.my-scroller .pull-to-refresh-layer .spinner-holder
  {
    visibility: hidden;
  }

  .my-scroller .pull-to-refresh-layer {
    background-image: url(http://qianka.b0.upaiyun.com/images/4f013b6bc7d96fc347f416ad3673f937.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: .8rem .8rem;

    opacity: 0;
    -webkit-transform: scale(1);
    transform: scale(1);

    transition: all .15s linear;
    -webkit-transition: all .15s linear;
  }

  .my-scroller .pull-to-refresh-layer.active {
    -webkit-transform: scale(1.35);
    transform: scale(1.35);
    opacity: 1;
  }
  
.type{
	width: 1.68rem;
	position: absolute;
	top: 0.9rem;
	bottom: 1rem;
	overflow-y: auto;
	ul{
		width: 100%;
		float: left;
		background: #f5f5f5;
		li{
			width: 100%;
			padding: 0 0.2rem;
			a{
				float: left;
				width: 100%;
				text-align: center;
				border-bottom: 1px solid #eeeeee;
				font-size: 0.26rem;
				color: #999998;
				span:nth-child(1){
					display: inline-block;
					width: 0.44rem;
					height: 0.44rem;
					opacity: 0.5;
					margin-top:0.2rem;
					img{
						width: 100%;
						height: 100%;
					}
				}
				span:nth-child(2){
					display: inline-block;
					width: 100%;
					text-align: center;
					padding-bottom: 0.2rem;
				}
			}
			.router-link-exact-active{
				border-bottom: 2px solid #ed5564;
				span:nth-child(1){
					opacity: 1;
				}
				span:nth-child(2){
					color: #ed5564;
					font-weight:bold;
				}
			}
		}
	}
}
</style>



