<template>
	<div class="page">
		<header-bar type="searchTop"></header-bar>
		<search-bar :list = "list" :his_list = "his_list"></search-bar>
	</div>

</template>

<script>
	import { mapState } from 'vuex'
	import HeaderBar from '../components/HeaderBar'
	import SearchBar from '../components/SearchBar'
	export default {
		name: 'SearchView',
		components: { HeaderBar,SearchBar },
		data() {
			return {
				
			}
		},
		computed: {
		    ...mapState({
		      list: state => state.search.list,
		      his_list: state => state.search.his_list
		    })
		},
		methods:{
			getList:function(){
				this.$store.dispatch('getList')
			}
		},
		created:function(){
			this.getList();
		}
	}
</script>

<style lang="scss" scoped>
	
</style>


