<template>
	<header>
			<a href="javascript:;" @click="goPre" class="toPre" v-if="type">
			</a>
			<router-link  :to="{ name: 'MyShoppingView'}" class="toHome" v-else>
			</router-link>
			<h3>{{title}}</h3>
			<router-link  :to="{ name: 'RegisterView'}" class="login" v-if="loginHeader">
				注册
			</router-link>
			<router-link  :to="{ name: 'LoginView'}" class="login" v-else>
				登录
			</router-link>
  	</header>
</template>

<script>
	export default {
		name: 'Banner',
		props: ["title","type",'loginHeader'],
		data() {
			return {}
		},
		methods:{
			goPre:function(){
				this.$router.go(-1)
			}
		}
	}
</script>

<style lang='scss' scoped>
header{
	width: 100%;
	height:0.9rem;
	line-height: 0.9rem;
	border-bottom: 1px solid #EEEEEE;
	.toPre{
		float: left;
		width: 0.36rem;
		height: 0.36rem;
		background-image: url(../assets/img/arrow_left_b.png);
		background-repeat: no-repeat;
		background-size: 100% 100%;
	    background-size: 90%;
	    opacity: 0.7;
	    margin: 0.26rem;
	}
	.toHome{
		float: left;
		width: 0.36rem;
		height: 0.36rem;
		background-image: url(../assets/img/home_b.png);
		background-repeat: no-repeat;
		background-size: 100% 100%;
	    background-size: 90%;
	    opacity: 0.7;
	    margin: 0.26rem;
	}
	h3{
		float: left;
		position: absolute;
		width: 2rem;
		left: 50%;
		margin-left: -1rem;
		text-align: center;
		font-weight: 400;
		
	}
	.login{
		float: right;
		margin-right: 0.4rem;
		color: #ED5564;
	}
}
</style>



