<template>
	<div class="list">
		<h3>{{title}}</h3>
		<ul>
			<li v-for ="item in items">
				<router-link :to="'goodsDetail/' + item.goods_id ">
					<img :src="item.goods_image"/>
					<p>{{item.goods_name}}</p>
					<span>￥{{item.goods_price}}</span>
				</router-link>
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'HomeList',
		props: ['title', 'items'],
		data() {
			return {}
		}
	}
</script>

<style lang='scss' scoped>
.list{
	width: 100%;
	background: #f0f0f0;
	float: left;
	h3{
		border-left: solid 0.06rem #ED5564;
	    height: 0.34rem;
	    margin: 0.22rem 0.12rem;
	    padding-left: 0.12rem;
	    font-size: 0.3rem;
	    line-height: 0.34rem;
	    font-weight: 400;
	}
	ul{
		width: 100%;
		li{
			width: 50%;
			padding: 0.24rem 0.16rem;
			float: left;
			a{
				float: left;
				width: 100%;
				background: white;
				border-radius:0.16rem ;
				img{
					width: 100%;
					height: 4.7rem;
					border-radius: 0.16rem 0.16rem 0 0;
				}
				p{
					width: 100%;
					padding-left: 0.2rem;
					line-height: 0.6rem;
					color: #000000;
					white-space: nowrap;
					text-overflow:ellipsis; 
					overflow:hidden;
				}
				span{
					display: block;
					color: #db4453;
					width: 3.1rem;
					height: 0.72rem;
					line-height: 0.72rem;
					border-top: 1px solid #eeeeee;
					margin: 0 auto;
				}
			}
		}
	}
}
</style>



