<template>
	<div class="register">
		<vue-form :state="formstate" v-model="formstate" @submit.prevent="onSubmit">
	  		<div class="group">
	  			<validate auto-label class="form-group required-field" :class="fieldClassName(formstate.name)">
		  			<label for="name">&nbsp;用&nbsp;户&nbsp;名</label>
		  			<input type="text" name="name" id="name" name-strength required v-model.lazy="model.name" placeholder="请输入6-20个字符"/>
	  			</validate>
	  		</div>
	  		<field-messages name="name" show="$touched || $submitted" class="form-control-feedback">
	          <div slot="required">用户名不能为空</div>
	          <div slot="name-strength">请输入6-20个字符</div>
	        </field-messages>
	        
	  		<div class="group">
	  			<validate auto-label class="form-group required-field" :class="fieldClassName(formstate.password)">
		  			<label for="password" required>设置密码</label>
		  			<input type="password" name="password" id="password" password-strength required v-model.lazy="model.password" placeholder="请输入6-20位密码"/>
	  			</validate>
	  		</div>
	  		 <field-messages auto-label name="password" show="$touched || $submitted" class="form-control-feedback">
	          <div slot="required">密码不能为空</div>
	          <div slot="password-strength">请输入6-20位密码</div>
	        </field-messages>
	        
	  		<div class="group">
	  			<validate auto-label class="form-group required-field" :class="fieldClassName(formstate.confirmPassword)">
		  			<label for="confirmPassword">确认密码</label>
		  			<input type="password" name="confirmPassword" id="confirmPassword" :matches="model.password" required v-model.lazy="model.confirmPassword" placeholder="请再次输入密码"/>
	  			</validate>
	  		</div>
	  		<field-messages auto-label name="confirmPassword" show="$touched || $submitted" class="form-control-feedback">
	          <div slot="required">确认密码不能为空</div>
	          <div slot="matches">两次密码不一致</div>
	        </field-messages>
	        
	  		<div class="group">
	  			<validate auto-label class="form-group required-field" :class="fieldClassName(formstate.email)">
		  			<label for="email">邮&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;箱</label>
		  			<input type="text" name="email" id="email" email-strength required v-model.lazy="model.email" placeholder="请输入常用邮箱地址"/>
	  			</validate>
	  		</div>
	        <field-messages name="email" show="$touched || $submitted" class="form-control-feedback">
	          <div slot="required">邮箱不能为空</div>
	          <div slot="email-strength">邮箱格式不正确</div>
	        </field-messages>
	  		<div class="remember-form">
		        <input id="checkbox" type="checkbox" :checked="isChecked" class="checkbox">
		        <label for="checkbox">同意</label>
		        <a class="reg-cms" href="javascript:;" target="_blank">用户注册协议</a> 
	  		</div>
	  		<button type="submit">注册</button>
  		</vue-form>
  	</div>
</template>

<script>
	export default {
		name: 'register',
		props: ["title"],
		data() {
			return {
				formstate: {},
			    model: {
			      name: '',
			      password: '',
			      confirmPassword: '',
			      email:''
			    },
     			isChecked:true
			}
		},
		methods:{
			fieldClassName: function (field) {
		    	// for bootstrap classes
		      if(!field) {
		        return '';
		      }
		      if((field.$touched || field.$submitted) && field.$valid) {
		        return 'has-success';
		      }
		      if((field.$touched || field.$submitted) && field.$invalid) {
		        return 'has-danger';
		      }
		    },
		    onSubmit: function() {
		    	if(this.formstate.$valid){
//		    		this.$http.jsonp('http://www.my9hao.com/mobile/index.php?act=login&op=register',{
//			  			username: this.model.name,
//	                    password: this.model.password,
//	                    password_confirm: this.model.confirmPassword,
//	                    email: this.model.email,
//	                    client: this.t
//		  			}).then(function(res){
//			    		console.log(res)
//					},function(err){
//						
//					});
		    	}
		      console.log(this.formstate.$valid);
		    }
		}
	}
</script>

<style lang='scss' scoped>
.form-control-feedback{
	width: 100%;
	text-align: right;
	padding-right: 0.3rem;
	color: #FF0000;
	line-height: 0.8rem;
	font-size: 0.30rem;
}
.register{
	width: 100%;
	margin-top: 0.22rem;
	.group{
		width: 100%;
		height: 0.86rem;
		line-height: 0.86rem;
		border-bottom: 1px solid #EEEEEE;
		background: white;
		label{
			float: left;
			vertical-align: middle;
			width: 1.32rem;
			height: 100%;
			margin-left: 0.3rem;
			text-align: right;
		}
		input{
			width: 5.52rem;
			float: left;
			height: 0.84rem;
			margin-left: 0.3rem;
			outline: none;
			border: none;
		}
	}
	
	.remember-form {
    height: 0.4rem;
    margin: 0.44rem 0.88rem 0 0.88rem;
    position: relative;
    input[type="checkbox"] {
    		    -webkit-appearance: none;
		    position: relative;
		    width: 0.4rem;
		    height: 0.4rem;
		    background-color: #F5F5F5;
		    border: 1px solid #CCC;
		    -webkit-border-radius: 50%;
		    border-radius: 50%;
		    vertical-align: middle;
		    outline: none;
		}
		 input[type="checkbox"]:checked {
		    border-color: #48CFAE;
		    background-color: #48CFAE;
		}
		input[type="checkbox"]:checked:after {
		    content: '';
		    position: absolute;
		    left: 0.05rem;
		    top: 0.06rem;
		    width: 0.2rem;
		    height: 0.1rem;
		    border-left: 0.03rem solid #fff;
		    border-bottom: 0.03rem solid #fff;
		    -webkit-transform: rotate(-45deg);
		    -ms-transform: rotate(-45deg);
		    transform: rotate(-45deg);
		}
		.reg-cms {
		    line-height: 0.4rem;
		    font-size: 0.26rem;
		    vertical-align: top;
		    display: inline-block;
		    height: 0.4rem;
		    color: #5D9CEC;
		}
	}
	button{
		width: 90%;
	    height: 0.8rem;
	    padding: 0;
	    margin: 0 5%;
	    font-size: 0.34rem;
	    line-height: 0.8rem;
	    text-align: center;
	    color: #888;
	    background: #EEE;
	    border: none;
	    border-radius: 0.2rem;
	    margin-top: 0.4rem;
	    outline: none;
	}
}	
</style>



