<template>
	<div class="sort">
		<ul>
			<li><a href="javascript:;">综合排序<i></i></a></li>
			<li><a href="javascript:;">销量优先</a></li>
			<li><a href="javascript:;">筛选<i></i></a></li>
		</ul>
		<div class="browse-mode" @click="change">
			<span><img src="../assets/img/browse_grid.png"/></span>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Sorting',
		props: {
			
		},
		data() {
			return {}
		},
		methods:{
			change:function(){
				this.$emit('changeList');
			}
		}
	}
</script>

<style lang='scss' scoped>
.sort{
	position: fixed;
	top: 0.9rem;
	width: 100%;
	height: 0.9rem;
	line-height: 0.9rem;
	border-bottom: 1px solid #eeeeee;
	background: white;
	z-index: 999;
	ul{
		width: 85%;
		height: 100%;
		float: left;
		text-align: center;
		li{
			float: left;
			height: 100%;
			a{
				color: #aaaaaa;
				i{
					font-size: 0px;
				    line-height: 0;
				    vertical-align: middle;
				    display: inline-block;
				    width: 0px;
				    height: 0px;
				    margin-left: 0.1rem;
				    border-width: 0.1rem;
				    border-color: #AAA transparent transparent transparent;
				    border-style: solid dashed dashed dashed;
				}
			}
		}
		li:nth-child(1){
			width: 40%;
		}
		li:nth-child(2){
			width: 30%;
		}
		li:nth-child(3){
			width: 30%;
		}
	}
	.browse-mode{
		float: right;
		width: 14.5%;
		height: 100%;
		border-left: 1px solid #eee;
		text-align: center;
		span{
			opacity: 0.5;
			img{
				width: 0.4rem;
				height: 0.4rem;
				vertical-align: middle;
				
			}
		}
	}
}
</style>



