<template>
	<div class="listTypes">
		<template v-if="isRecommend">
			<classification-main :items = 'recommends' type="recommends"></classification-main>
		</template>
		<template v-else>
			<classification-main :items = 'goodsNameList' type="goodsNameList"></classification-main>
		</template>
	</div>

</template>

<script>
	import { mapState } from 'vuex'
	import GoodsTypes from '../components/GoodsTypes'
	import ClassificationMain from '../components/ClassificationMain'
	export default {
		name: 'ClassificationView',
		components: { GoodsTypes,ClassificationMain },
		data() {
			return {
				isRecommend:true,
				id:''
			}
		},
		computed: {
		    ...mapState({
		      recommends: state => state.classification.recommends,
		      goodsNameList: state => state.classification.goodsNameList
		    })
		},
		methods:{
			getTypeList: function () {
				this.id = this.$route.params.id;
				if(this.id === '001'){
					this.$store.dispatch('getRecommends');
					this.isRecommend = true;
				}else{
					this.isRecommend = false;
					this.$store.dispatch({
				      type: 'getGoodsNameList',
				      id: this.id
				    }).then(res => {
				     
				    })
				}
		      	
		   }
		},
		created(){
			this.getTypeList()
			
		},
		watch: {
		  '$route' (to, from) {
		  //刷新参数放到这里里面去触发就可以刷新相同界面了
		    this.getTypeList()
		  }
		}
	}
</script>

<style lang="scss" scoped>
	.listTypes{
		width: 5.82rem;
		background: white;
		position: absolute;
		top: 0.9rem;
		right: 0;
		bottom: 1rem;
		overflow-y: auto;
	}
</style>


