<template>
  <div id="submit_code">
  	<register-header title="提交验证码" type="getGo"></register-header>
  	<p>请输入<span>{{this.phone}}</span>收到的短信验证码</p>
  	<div class="group">
  			<label for="code">&nbsp;动&nbsp;态&nbsp;码</label>
  			<input type="text" name="code" id="code"   v-model="code" placeholder="请输入短信动态验证码!"/>
  			<a href="javascript:;" @click="getCode">{{this.countdown}}</a>
		</div>
		<button @click="nextBtn">下一步</button>
		<p class="ts">绑定手机不收任何费用，一个手机只能绑定一个账号，若需修改或解除已绑定的手机，请登录商城PC端进行操作。</p>
		<transition name="fade">
			<div class="error" v-if="show">
				{{this.error}}	
			</div>
	  </transition>
  </div>
</template>

<script>
import RegisterHeader from '../components/RegisterHeader'

export default {
  name: 'RegisterView',
  components: { RegisterHeader },
  data () {
    return {
     	phone:'',
     	sec_key:'',
     	sec_val:'',
     	code:'',
     	error:'',
     	show:false,
     	countdown:'再次短信获取',
     	timer:0,
     	num:120,
     	isDaoJiShi:true
    }
  },
  methods: {
	  getCode:function(){
	  	const that = this
	  	if(this.isDaoJiShi){
	  		this.$http.jsonp('http://www.my9hao.com/mobile/index.php?act=connect&op=get_sms_captcha&type=1&phone='+this.phone+'&sec_val='+this.sec_val+'&sec_key='+this.sec_key)
		   		.then(function(res){
		    		if(res.body.datas.sms_time){
		    			this.show = false
		    			this.countdownMethod()
		    		}else{
		    			this.error = res.body.datas.error
		    			this.show = true
		    			setTimeout(function(){
		    					that.show = false
		    			},1500)
		    		}
				},function(err){
					
				});
	  	}else{
	  		
	  	}
	  },
	  countdownMethod:function(){
	  	const that = this
	  	this.isDaoJiShi = false
	  	this.timer = setInterval(function(){
	  		that.num--
	  		that.countdown = that.num +'s后重新获取'
	  		if(that.num == 0){
	  			clearInterval(that.timer)
	  			that.countdown = '再次短信获取'
	  			this.isDaoJiShi = true
	  		}
	  	},1000)
	  },
	  nextBtn:function(){
	  	this.$http.jsonp('http://www.my9hao.com/mobile/index.php?act=connect&op=check_sms_captcha&type=1&phone='+this.phone+'&captcha='+this.code)
		   		.then(function(res){
		    		console.log(res.body.datas)
		    		
				},function(err){
					
				});
	  }
  },
  mounted:function(){
  	this.phone = this.$route.query.phone
  	this.sec_key = this.$route.query.sec_key
  	this.sec_val = this.$route.query.sec_val
  	this.getCode()
  }
}
</script>
<style lang="scss" scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s ease
}
.fade-enter, .fade-leave-to{
  opacity: 0
}
#submit_code{
	width: 100%;
	height: 100%;
	p{
		padding: 0.28rem 0.44rem;
		color: #888;
		span{
			color: #000000;
		}
	}
	.group{
		width: 100%;
		height: 0.86rem;
		line-height: 0.86rem;
		border-bottom: 1px solid #EEEEEE;
		position: relative;
		background: white;
		margin-top: 0.22rem;
		label{
			float: left;
			vertical-align: middle;
			width: 1.32rem;
			height: 100%;
			margin-left: 0.3rem;
			text-align: right;
		}
		input{
			width: 5.52rem;
			float: left;
			height: 0.84rem;
			margin-left: 0.3rem;
			outline: none;
			border: none;
		}
		a{
			position: absolute;
			right: 0.4rem;
			width: 2rem;
			height: 0.6rem;
			line-height: 0.6rem;
			top: 50%;
			margin-top: -0.3rem;
			color: #3BAEDA;
			text-decoration: underline;
			font-weight: bold;
			text-align: center;
			font-size: 0.24rem;
		}
	}
	button{
		width: 90%;
    height: 0.8rem;
    padding: 0;
    margin: 0 5%;
    font-size: 0.34rem;
    line-height: 0.8rem;
    text-align: center;
    color: #888;
    background: #EEE;
    border: none;
    border-radius: 0.2rem;
    margin-top: 0.4rem;
    outline: none;
	}
	.ts{
		font-size: 0.24rem;
		padding: 0.28rem 0.7rem;
	}
	.error{
		background-color: rgba(255,255,255,0.95);
    border: solid 1px #CCC;
    padding: 0.36rem 0.28rem;
    text-align: center;
    border-radius: 8px;
    position: fixed;
    z-index: 9999;
    top: 40%;
    left: 25%;
    right: 25%;
    color: #333;
    font-size: 0.3rem;
    line-height: 0.44rem;
    box-shadow: 0 0 0.44rem rgba(0,0,0,0.15);
	}
}
</style>



