<template>
  <div id="login_view">
  	<register-header title="登录" loginHeader="注册"></register-header>
  	<vue-form :state="formstate" v-model="formstate" @submit.prevent="onSubmit">
  		<div class="group">
  			<validate auto-label class="form-group required-field" :class="fieldClassName(formstate.name)">
	  			<label for="name">&nbsp;账&nbsp;&nbsp;&nbsp;户</label>
	  			<input type="text" name="name" id="name" name-strength required v-model.lazy="model.name" placeholder="已验证手机"/>
	  		</validate>	
  		</div>
  		<field-messages name="name" show="$touched || $submitted" class="form-control-feedback">
          <div slot="required">手机号码不能为空</div>
          <div slot="name-strength">不是正确的手机号</div>
        </field-messages>
        
  		<div class="group">
  			<validate auto-label class="form-group required-field" :class="fieldClassName(formstate.password)">
	  			<label for="password">&nbsp;密&nbsp;&nbsp;&nbsp;码</label>
	  			<input type="password" name="password" id="password" password-strength required v-model.lazy="model.password" placeholder="登录密码"/>
	  		</validate>		
  		</div>
  		
    <field-messages name="password" show="$touched || $submitted" class="form-control-feedback">
      <div slot="required">密码不能为空</div>
      <div slot="password-strength">请输入6-20位密码</div>
    </field-messages>
  		<div class="remember-form">
	        <input id="checkbox" type="checkbox" :checked="isChecked" class="checkbox">
	        <label for="checkbox">同意</label>
	        <a class="reg-cms" href="javascript:;" target="_blank">七天自动登录</a> 
	        <router-link  :to="{ name: 'MyShoppingView'}" style="float: right;color:#4B89DC;">
	        	忘记密码?
					</router-link>
  		</div>
  		<button type="submit">登录</button>
  	</vue-form>	
  </div>
</template>

<script>
import RegisterHeader from '../components/RegisterHeader'
export default {
  name: 'LoginView',
  components: { RegisterHeader },
  data () {
    return {
    	formstate: {},
			isChecked:true,
			model: {
		      name: '',
		      password: ''
	    },
	    codeKey:''
    }
  },
  mounted:function(){
  	
  },
  methods:{
  	fieldClassName: function (field) {
    	// for bootstrap classes
      if(!field) {
        return '';
      }
      if((field.$touched || field.$submitted) && field.$valid) {
        return 'has-success';
      }
      if((field.$touched || field.$submitted) && field.$invalid) {
        return 'has-danger';
      }
   	},
		onSubmit: function() {
	    	if(this.formstate.$valid){
	    		
	    	}
//	      console.log(this.formstate.$valid);
	  }
  	
  }
}
</script>

<style lang="scss" scoped>
	.form-control-feedback{
	width: 100%;
	text-align: right;
	padding-right: 0.3rem;
	color: #FF0000;
	line-height: 0.8rem;
	font-size: 0.30rem;
}
#login_view{
	width: 100%;
	margin-top: 0.22rem;
	.group{
		width: 100%;
		height: 0.86rem;
		line-height: 0.86rem;
		border-bottom: 1px solid #EEEEEE;
		position: relative;
		background: white;
		label{
			float: left;
			vertical-align: middle;
			width: 1.32rem;
			height: 100%;
			margin-left: 0.3rem;
			text-align: right;
		}
		input{
			width: 5.52rem;
			float: left;
			height: 0.84rem;
			margin-left: 0.3rem;
			outline: none;
			border: none;
		}
		img{
			position: absolute;
			right: 0.4rem;
			width: 1.76rem;
			height: 0.6rem;
			top: 50%;
			margin-top: -0.3rem;
			
		}
	}
	.remember-form {
	    height: 0.4rem;
	    margin: 0.44rem 0.88rem 0 0.88rem;
	    position: relative;
	    input[type="checkbox"] {
    		    -webkit-appearance: none;
		    position: relative;
		    width: 0.4rem;
		    height: 0.4rem;
		    background-color: #F5F5F5;
		    border: 1px solid #CCC;
		    -webkit-border-radius: 50%;
		    border-radius: 50%;
		    vertical-align: middle;
		    outline: none;
		}
		 input[type="checkbox"]:checked {
		    border-color: #48CFAE;
		    background-color: #48CFAE;
		}
		input[type="checkbox"]:checked:after {
		    content: '';
		    position: absolute;
		    left: 0.05rem;
		    top: 0.06rem;
		    width: 0.2rem;
		    height: 0.1rem;
		    border-left: 0.03rem solid #fff;
		    border-bottom: 0.03rem solid #fff;
		    -webkit-transform: rotate(-45deg);
		    -ms-transform: rotate(-45deg);
		    transform: rotate(-45deg);
		}
		.reg-cms {
		    line-height: 0.4rem;
		    font-size: 0.26rem;
		    vertical-align: top;
		    display: inline-block;
		    height: 0.4rem;
		    color: #5D9CEC;
		}
	}
	button{
		width: 90%;
    height: 0.8rem;
    padding: 0;
    margin: 0 5%;
    font-size: 0.34rem;
    line-height: 0.8rem;
    text-align: center;
    color: white;
    background: #ED5564;
    border: none;
    border-radius: 0.2rem;
    margin-top: 0.4rem;
    outline: none;
	}
}	
</style>



