<template>
	<div class="searchMain">
		<dl>
			<dt>热门搜索</dt>
			<dd v-for="(li,index) in list">
				<router-link :to="'/pages/classification/search/goodsList/' + list[index]">
				{{list[index]}}
				</router-link>
			</dd>
		</dl>
		<dl>
			<dt>历史记录</dt>
			<dd v-for="(li,index) in his_list">
				<router-link :to="'/pages/classification/search/goodsList/' + his_list[index]">
					{{his_list[index]}}
				</router-link>
			</dd>
		</dl>
		<a href="javascript:;" class="clear-history">清空历史</a>
	</div>
</template>

<script>
	export default {
		name: 'HeaderBar',
		props: ["list","his_list"],
		data() {
			return {}
		}
	}
</script>

<style lang='scss' scoped>
.searchMain{
	width: 100%;
	height: 100%;
	margin-top: 0.9rem;
	background: #f5f5f5;
	dl{
		padding: 0.34rem 0.22rem 0.22rem;
		width: 100%;
		float: left;
		dt{
			width: 100%;
			height: 0.64rem;
		}
		dd{
			float: left;
			padding: 0.09rem 0.22rem;
			border-radius: 0.5rem;
			background: white;
			margin-right: 0.18rem;
			margin-bottom: 0.18rem;
			font-size: 0.26rem;
			a{
				color: #333;
			}
		}
	}
	dl:nth-child(2){
		margin-bottom: 0.5rem;
		dd{
			border-radius: 0;
		}
	}
	> a{
		display: block;
		clear: both;
		width: 5.5rem;
		height: 0.84rem;
		line-height: 0.84rem;
		text-align: center;
		margin:  0 auto;
		background: #eee;
		color: #555;
		border-radius: 0.2rem;
		
	}
}

</style>



