import Vue from 'vue'

const state = {
  goodsTitleList: [],
  recommends:[],
  goodsNameList:[]
}

const mutations = {
  getGoodsTitleList (state, payload) {
    state.goodsTitleList = payload.res.class_list
  },
  getRecommends (state, payload) {
    state.recommends = payload.res.brand_list
  },
  getGoodsNameList (state, payload) {
    state.goodsNameList = payload.res.class_list
  }
}

const actions = {
  getGoodsTitleList ({ commit }) {
    Vue.http.jsonp('http://www.my9hao.com/mobile/index.php?act=goods_class')
            .then(res => {
              commit({
                type: 'getGoodsTitleList',
                res: res.body.datas
              })
            })
  },
  getRecommends ({ commit } , payload) {
    Vue.http.jsonp('http://www.my9hao.com/mobile/index.php?act=brand&op=recommend_list')
            .then(res => {
              commit({
                type: 'getRecommends',
                res: res.body.datas
              })
            })
  },
  getGoodsNameList ({ commit } , payload) {
    Vue.http.jsonp('http://www.my9hao.com/mobile/index.php?act=goods_class&op=get_child_all&gc_id='+payload.id)
            .then(res => {
              commit({
                type: 'getGoodsNameList',
                res: res.body.datas
              })
              
            })
  }
}

export default {
  state,
  mutations,
  actions
}



