<template>
	<div class="nctouch_footer">
		<ul>
			<li>
		  		<router-link  :to="{ name: 'LoginView'}" >
		  				登录
		        </router-link>
		  	</li>
		  	<li>
		  		<router-link  :to="{ name: 'RegisterView'}">
		               	注册
		       </router-link>
		  	</li>
		  	<li>
		  		<router-link  :to="{ name: 'RegisterView'}">
		               	反馈
		       </router-link>
		  	</li>
		  	<li>
		  		<router-link :to="{ name: 'RegisterView'}">
		              	帮助
		     	</router-link>
		  	</li>
		  	<li>
		  		<a href="javascript:;" @click="toTop">
		             	返回顶部
		     	</a>
		  	</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'NctouchFooter',
		data() {
			return {
				
			}
		},
		methods:{
		 	toTop:function(){
		 		window.scrollTo(0,0)
		 		this.$emit("top");  
		 	}
		}
	}
</script>

<style lang='scss' scoped>
.nctouch_footer{
	width: 100%;
	height: 0.84rem;
	float: left;
	background: white;
	ul{
		width: 100%;
		padding: 0 0.35rem;
		li{
			width: 1.36rem;
			height: 0.38rem;
			float: left;
			text-align: center;
			margin-top: 0.24rem;
			a{
				display: block;
				width: 100%;
				height: 100%;
				border-left: 1px solid #eee;
				color: #555;
			}
		}
		li:nth-child(1){
			a{
				border-left: none;
			}
		}
	}
}
</style>



